import React from "react";
import "./Navbar.css";

import code from "./icons/code.svg";
import certificate from "./icons/certificate.svg";
import follow from "./icons/follow.svg";

import { Link } from "react-router-dom";

export default class Navbar extends React.Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  render() {
    return (
      <nav>
        <Link to="/" title="Home">
          <div style={{ cursor: "pointer" }}>Alexanderbira</div>
        </Link>

        <div id="icons">
          <Link to="/demos" title="Demos">
            <img
              src={code}
              alt="code"
              draggable="false"
              onMouseDown={(e) => e.preventDefault()}
            />
          </Link>
          <Link to="/certificates" title="Certificates">
            <img
              src={certificate}
              alt="certificate"
              draggable="false"
              onMouseDown={(e) => e.preventDefault()}
            />
          </Link>
          <Link to="/follow" title="Follow">
            <img
              src={follow}
              alt="follow"
              draggable="false"
              onMouseDown={(e) => e.preventDefault()}
            />
          </Link>
        </div>
      </nav>
    );
  }
}
