import React from "react";
import "./App.css";
import Navbar from "../Navbar/Navbar.js";

import Home from "../Home/Home.js";
import Demos from "../Demos/Demos.js";
import Certificates from "../Certificates/Certificates.js";
import Follow from "../Follow/Follow.js";

import { HashRouter as Router, Route, Switch } from "react-router-dom";

export default class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  render() {
    return (
      <Router basename={process.env.PUBLIC_URL}>
        <Navbar />

        <Switch>
          <Route exact path="/" render={() => <Home />} />
          <Route path="/demos" render={() => <Demos />} />
          <Route path="/certificates" render={() => <Certificates />} />
          <Route path="/follow" render={() => <Follow />} />
          <Route status={404} render={() => <h1>404 not found!</h1>} />
        </Switch>
      </Router>
    );
  }
}
